import React from 'react'
import projarr from './Projectarr'


const Projects = (props) => {

    
    return(
        <div>
            {
                projarr.map((ele) =>{
                    return (
                    <div className="projects">
                            <h1 className="projname">{ele.name}</h1>
                            <p className="projdesc">
                            <span>{ele.desc}</span>
                            </p>
                            <a href={ele.link1} className="projlink">Github Link</a>
                            <a href={ele.link2} className="projlink">Website Link</a>
                    </div>
                
                    )

                })
                
            }
        </div>
    )
    }
export default Projects