import React from 'react'
import Arr from './Skillsarr'



const Skills = (props) => {
    return(
        <div>
            {
                Arr.map((ele) =>{
                    return (<div className="skillset">
                        <img src={ele.src} width={ele.width} alt = {ele.alt} ></img>
                        <p className="tool">{ele.name}</p>
                    </div>)

                })
                
            }
        </div>
           
           
           
        
    )
}

export default Skills